/*
=============== 
Variables
===============
*/
$mainColor: #ff6200;

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(34, 100%, 50%);
  --clr-primary-2: hsl(46, 100%, 50%);
  --clr-primary-3: hsl(27, 100%, 50%);
  --clr-primary-4: hsl(36, 100%, 50%);
  /* primary/main color */
  --clr-primary-5: hsl(34, 100%, 51%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(49, 90%, 70%);
  --clr-primary-7: hsl(44, 90%, 76%);
  --clr-primary-8: hsla(31, 100%, 49%, 0.319);
  --clr-primary-9: hsl(36, 95%, 48%);
  --clr-primary-10: hsl(204, 100%, 48%);
}
/*
=============== 
Global Styles
===============
*/

/*  global classes */

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}

/*
=============== 
Navbar
===============
*/

.nav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}
.nav-toggle {
  font-size: 1.5rem;
  color: var(--clr-primary-5);
  background: transparent;
  border-color: transparent;
  transition: all 0.3s linear;
  cursor: pointer;
}
.nav-toggle:hover {
  color: var(--clr-primary-1);
  transform: rotate(90deg);
}
.logo {
  width: 190px;
  height: 80px;
}
.links a {
  color: var(--clr-grey-3);
  font-size: 1rem;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
  display: block;
  padding: 0.5rem 1rem;
  transition: all 0.3s linear;
}
.links a:hover {
  background: var(--clr-primary-8);
  color: var(--clr-primary-5);
  padding-left: 1.5rem;
}
.social-icons {
  display: none;
}
.links-container {
  height: 0;
  overflow: hidden;
  transition: all 0.3s linear;
}
.show-container {
  height: 10rem;
}
@media screen and (min-width: 800px) {
  .nav-center {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }
  .nav-header {
    padding: 0;
  }
  .nav-toggle {
    display: none;
  }
  .links-container {
    height: auto !important;
  }
  .links {
    display: flex;
  }
  .links a {
    padding: 0;
    margin: 0 0.5rem;
  }
  .links a:hover {
    padding: 0;
    background: transparent;
  }
  .social-icons {
    display: flex;
  }
  .social-icons a {
    margin: 0 0.5rem;
    color: var(--clr-primary-5);
    transition: all 0.3s linear;
  }
  .social-icons a:hover {
    color: var(--clr-primary-7);
  }
}
